import React, { Component } from "react";
import { Col, Container, Row } from "reactstrap";  
import HomeUrl from '../assets/images/home-border.png';

export default class Services extends Component {
  constructor(props) {
    super(props);
    this.state = {
      services: [
        {
          id: 'service1',
          icon: 'mdi-google',
          title: "Data cleaning",
          description: "Is your data messed up?. Don't worry, we can clean it up for you.",
        },
        {
          id: 'service2',
          icon: 'mdi-instagram',
          title: "Instagram Ads",
          description: 'With our Instagram Ads services, you can get more followers and likes.',
        },
        {
          id: 'service3',
          icon: 'mdi-youtube',
          title: "Youtube Ads",
          description: 'We provide best Youtube Ads services for your business.',
        },
      ],
 
    };
  }

  render() {
    return (
      <React.Fragment>
        <section className="section bg-services" id="services">
          <Container>
            <Row>
              <Col lg="12">
                <div className="title-box text-center">
                  <h3 className="title-heading mt-4">Provide Awesome Services </h3>
                  <p className="text-muted f-17 mt-3"> Have a look at our additional service we provide apart 
                  from automation <br /> </p>

                  <img src={HomeUrl} height="15" className="mt-3" alt="" />
                </div>
              </Col>
            </Row>
            <Row className="mt-5 pt-4">
              {/* Render Footer Link */}
              {this.state.services.map((item, key) => (
                <Col lg={4} key={key}>
                  <div className="services-box p-4 mt-4">
                    <div className="services-icon bg-soft-primary">
                      <i className={"mdi text-primary " + item.icon}></i>
                    </div>

                    <h5 className="mt-4">{item.title}</h5>
                    <p className="text-muted mt-3">{item.description}</p>

                  </div>
                </Col>
              ))}
            </Row>
                
          </Container>
        </section>
      </React.Fragment>
    );
  }
}
