import React from "react";
import { Container, Row, Col, FormGroup,CustomInput, Label, Input ,Button,ModalBody,ModalHeader, Modal, ModalFooter,Spinner } from "reactstrap";
import axios from 'axios';
import {QRCodeSVG} from 'qrcode.react';
import * as XLSX from 'xlsx';

//import images
import HomeUrl from '../../assets/images/home-border.png';

const MAX_FILE_SIZE = 5; // 5 MB

const Section = ()=>{
  const [state,setState] = React.useState({isOpen:false});
  const [valid,setValid] = React.useState(false);
  const [file,setFile] = React.useState(null);
  const [message,setMessage] = React.useState('');
  const [loading,setLoading] = React.useState(false);
  const [error,setError] = React.useState('');
  // state for response
  var timer = null;
  const [qrData,setQrData] = React.useState('');
  const [qrModal,setQrModal] = React.useState(false);



  function isValidExcelFile(e){
    let allowed_mimetype=['application/vnd.ms-excel.sheet.macroEnabled.12','application/vnd.ms-excel','application/vnd.openxmlformats-officedocument.spreadsheetml.sheet','text/csv','application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'];
    let file_mimetype=e.target.files[0].type;
    if(allowed_mimetype.includes(file_mimetype)){ 
 
      const reader = new FileReader();
      reader.onload = (evt) => { // evt = on_file_select event
          /* Parse data */
          const bstr = evt.target.result;
          const wb = XLSX.read(bstr, {type:'binary'});
          /* Get first worksheet */
          const wsname = wb.SheetNames[0];
          const ws = wb.Sheets[wsname];
          /* Convert array of arrays */
          const data = XLSX.utils.sheet_to_json(ws);
          // get first object and check if it has contact column or not
          if(data[0].hasOwnProperty('contact')){
            // check file size
            if(e.target.files[0].size <= MAX_FILE_SIZE * 1024 * 1024){

              // check if there is more than 10000 records 
              if(data.length <=10000 && data.length>=1){
                setValid(true);
                setFile(e.target.files[0]);
                setError('');
              }else{
                setError('File should have less than 10000 records');
              }
            } else {
              setError('File size must be less than 5 mb')
            }
          }else{
            setError("The file must contain 'contact' column");
          }

      };
      reader.readAsBinaryString(e.target.files[0]);
    }else{ 
      setError('Please select a valid excel/csv file');
      setValid(false);
    }
  }

  function openModal () {
    setState({isOpen: true})
  }

  function closeModal(){
    setState({isOpen:false})
  }

  async function uploadFile(){
    setLoading(true);
    try { 
      // check if valid is true or false
      if(valid===false){
        return;
      }

      let formData = new FormData();
      formData.append('contact', file);
      formData.append('message', message);
      const response = await axios.post('http://54.208.184.151:5000/file', formData, {
        headers: { "Content-Type": "multipart/form-data" }
      }); 
      let data = response.data;
      if(data.err==false){
        setQrModal(true);
        setQrData(data.data);
        timer = Date.now();
        setTimeout(()=>{ 
            setQrModal(false); 
        },60000);
      }else{ 
        setQrModal(false);
      } 
    } catch (error) { 
      setQrModal(false);
       
    } finally{
      setLoading(false);
    }
  }
  
  function handleMessage(e){
    setMessage(e.target.value);
  }

  function qrToggle(){ 
    setQrModal(!qrModal);
  }

    return (
      <React.Fragment>
        <section className="bg-home bg-light" id="home">
          <div className="home-center">
            <div className="home-desc-center">
              <Container>
                <Row className="align-items-center">
                  <Col lg={6}>
                    <div className="home-content">
                      <p className="mb-0">Automate Your Task</p>
                      <img src={HomeUrl} height="15" alt="" />
                      <h1 className="home-title mt-4">Grow Your <br /> Business with our hassle free, service.  </h1>
                      <p className="text-muted mt-4 f-20"> With automate anywhere automation becomes convenient and time saving with just 3 step.</p>
                      <div className="mt-4 pt-2">
                        <span to="#" className="btn btn-primary mr-3" onClick={openModal}>Tutorial</span>
                        {/* <Link to="#" className="video-play-icon text-dark" >
                          <i className="mdi mdi-play-circle-outline text-dark mr-2"></i>Watch Video
                          </Link> */}
                      </div>
                    </div>
                  </Col>
                  <Col lg={5} className="offset-lg-1">
                    <div className="home-registration-form bg-white p-5 mt-4">
                      <h5 className="form-title mb-4 text-center font-weight-bold">Send your message in one click.</h5>
                      <div className="registration-form">
                      <FormGroup>
                        <CustomInput
                          style={{cursor:'pointer'}}
                          type="file"
                          id="exampleCustomFileBrowser"
                          name="customFile"
                          label={'Upload Excel Sheet'} 
                          onChange={isValidExcelFile}
                          required
                           />
                      </FormGroup>
                        <Label className="text-muted">
                          Message
                        </Label>
                        <Input onChange={handleMessage} type="textarea" id="exampleInputName2" className="form-control mb-4 registration-input-box" />
                          {error && <span className="text-danger">{error}</span>}
                        <Button  disabled={(valid===false) || (message.length<5===true) || (loading===true)} onClick={uploadFile} color="primary" className="btn btn-primary w-100" block>{loading===true?<Spinner />:"Start"}</Button>
                      </div>
                    </div>
                  </Col>
                </Row>
              </Container>
              <Modal isOpen={state.isOpen} backdrop={true} centered={true} toggle={closeModal}>
                <ModalHeader toggle={closeModal}>Follow this step to start using our service</ModalHeader>

                <ModalBody>
                  <div>
                    <p>1. Create excel file and save all your whatsapp number under 'contact' column</p>
                  </div>

                  <div>
                    <p>2. Upload your excel file with your message</p>
                  </div>

                  <div>
                    <p>3. Open your whatsapp</p>
                  </div>

                  <div>
                    <p>4. Tap on Linked device</p>
                  </div>

                  <div>
                    <p>5. Scan qr code from your smartphone </p>
                  </div>
                </ModalBody>

                <ModalFooter>
                  <div>
                    <p>That's it your message will be send to your contact soon and will be logout from our system shortly.</p>
                  </div>
                </ModalFooter>
              </Modal>

              <Modal isOpen={qrModal} backdrop={true} centered={true} toggle={qrToggle}>
                <ModalHeader toggle={qrToggle}>
                  Scan this qr code from your whatsapp to proceed
                </ModalHeader> 
                <ModalBody > 
                    <div style={{display:"flex",justifyContent:"center"}}>
                      <QRCodeSVG value={qrData}/>
                    </div>
                </ModalBody>
                <ModalFooter>
                  If you find any difficulty in sending message you can always write to us.
                </ModalFooter>
              </Modal>
            </div>
          </div>
        </section>
      </React.Fragment>
    );
 
}

export default Section;
