import React, { Component } from "react";

import { Col, Container, Form, FormGroup, Input, Label, Row,Spinner } from "reactstrap";

import HomeUrl from "../assets/images/home-border.png";
import Feature from "../assets/images/features/img-3.png";

import axios from "axios";

const Contact = () => {
  const [form, setForm] = React.useState({
    fname: "",
    lname: "",
    email: "",
    message: "",
  });
  const [response,SetResponse] = React.useState('');
  const [loading, setLoading] = React.useState(false);
  async function handeSubmit(e) {
    e.preventDefault();
    setLoading(true);
    try {
      const data = form;
      const response = await axios.post(
        "https://formspree.io/",
        data
      );
    } catch (error) {
      console.log("There was some error");
    }finally{
      setLoading(false);
      SetResponse('Message sent successfully');
      setForm({
        fname: "",
        lname: "",
        email: "",
        message: "",
      })
    }
  }

  return (
    <React.Fragment>
      <section className="section" id="contact">
        <Container>
          <Row>
            <Col lg={12}>
              <div className="title-box text-center">
                <h3 className="title-heading mt-4">
                  Let's talk about everything!
                </h3>
                <p className="text-muted f-17 mt-3">
                  Do you have a question in your head?
                  <br /> Paste it in here we will surely look up for solution
                </p>
                <img src={HomeUrl} height="15" className="mt-3" alt="" />
              </div>
            </Col>
          </Row>
          <Row className="mt-5 pt-4">
            <Col lg={6}>
              <div className="mt-4 home-img text-center">
                <div className="animation-2"></div>
                <div className="animation-3"></div>
                <img src={Feature} className="img-fluid" alt="" />
              </div>
            </Col>
            <Col lg={6}>
              <div className="custom-form mt-4">
                <div id="message"></div>
                <Form
                  method="post"
                  name="contact-form"
                  id="contact-form"
                  onSubmit={handeSubmit}
                >
                  <Row>
                    <Col lg={6}>
                      <FormGroup className="mt-3">
                        <Label className="contact-lable">First Name</Label>
                        <Input
                          name="name"
                          id="name"
                          className="form-control"
                          type="text"
                          onChange={(e) => {
                            let value = e.target.value;
                            setForm((prevState) => ({
                              ...prevState,
                              fname: value,
                            }));
                          }}
                        />
                      </FormGroup>
                    </Col>
                    <Col lg={6}>
                      <FormGroup className="mt-3">
                        <Label className="contact-lable">Last Name</Label>
                        <Input
                          name="name"
                          id="lastname"
                          className="form-control"
                          type="text"
                          onChange={(e) => {
                            let value = e.target.value;
                            setForm((prevState) => ({
                              ...prevState,
                              lname: value,
                            }));
                          }}
                        />
                      </FormGroup>
                    </Col>
                  </Row>
                  <Row>
                    <Col lg={12}>
                      <FormGroup className="mt-3">
                        <Label className="contact-lable">Email Address</Label>
                        <Input
                          name="email"
                          id="email"
                          className="form-control"
                          type="text"
                          onChange={(e) => {
                            let value = e.target.value;
                            setForm((prevState) => ({
                              ...prevState,
                              email: value,
                            }));
                          }}
                        />
                      </FormGroup>
                    </Col>
                  </Row>
                  <Row>
                    <Col lg={12}>
                      <FormGroup className="mt-3">
                        <Label className="contact-lable">Your Message</Label>
                        <Input
                          type="textarea"
                          name="comments"
                          id="comments"
                          rows="5"
                          className="form-control"
                          onChange={(e) => {
                            let value = e.target.value;
                            setForm((prevState) => ({
                              ...prevState,
                              message: value,
                            }));
                          }}
                        />
                      </FormGroup>
                    </Col>
                  </Row>
                  <Row>
                    <Col lg={12} className="mt-3 text-right">
                        {response?<span className="text-success" style={{marginRight:"10px"}}>
                          {response}
                        </span>:''}
                      {
                        loading===true?
                        <Spinner color="primary" /> 
                        :<Input
                        id="submit"
                        name="send"
                        color="primary"
                        className="submitBnt btn btn-primary btn-round"
                        value="Send Message"
                        type="submit"
                        style={{ width: "auto", color: "#fff" }}
                        disabled={
                          form.fname.length === 0 ||
                          form.lname.length === 0 ||
                          form.email.length === 0 ||
                          form.message.length === 0
                        }
                      />
                      }
                      {" "}
                      <div id="simple-msg"></div>
                    </Col>
                  </Row>
                </Form>
              </div>
            </Col>
          </Row>
        </Container>
      </section>
    </React.Fragment>
  );
};

export default Contact;
